const API_URL = "https://awcu2nks23.us-east-1.awsapprunner.com";
const load = async ({ params }) => {
  const response = await fetch(`${API_URL}/api/${params.event}/pools`);
  if (response.status == 404) {
    return {
      games: null
    };
  }
  const data = await response.json();
  return {
    games: data
  };
};
export {
  load
};
